.publish-guide-popover.components-popover {
  z-index: 2;
}
.publish-guide-popover.components-popover .components-popover__content > div {
  overflow: hidden;
}
.publish-guide-popover__items {
  list-style-type: none;
  margin: 0;
  min-height: 500px;
  overflow: hidden;
  padding: 0;
  width: 350px;
}
.publish-guide-popover__item {
  border-bottom: 1px solid #f0f0f0;
  display: flex;
  margin-bottom: 0;
  padding: 22px 22px 22px 20px;
  transition: background-color 0.4s;
}
.publish-guide-popover__item:last-of-type {
  border-bottom: 0;
}
.publish-guide-popover__item--highlight {
  background-color: rgba(216, 239, 239, 0.4);
  padding-bottom: 30px;
  padding-top: 30px;
}
.publish-guide-popover__item__controls {
  margin-right: 2px;
}
.publish-guide-popover__item__controls .components-base-control,
.publish-guide-popover__item__controls .components-base-control__field {
  margin-bottom: 0 !important;
}
.publish-guide-popover__item__controls .components-checkbox-control__input[type=checkbox] {
  border-width: 2px;
}
.publish-guide-popover__item__controls .components-checkbox-control__input[type=checkbox]:checked {
  background-color: var(--wp-admin-theme-color);
}
.publish-guide-popover__item__controls .components-checkbox-control__checked {
  height: 20px;
  left: 0;
  top: 0;
  width: 20px;
}
.publish-guide-popover__item__inner-content {
  margin-top: 0.9em;
}
.publish-guide-popover__item__title {
  color: #1e1e1e;
  font-size: 1.1em;
  font-weight: 400;
  margin: 0;
  padding-top: 2px;
}
.publish-guide-popover__item__text {
  color: #757575;
  font-size: 1em;
  margin-bottom: 0;
  margin-top: 0.7em;
}
.publish-guide-popover__item__cta.components-button {
  color: var(--wp-admin-theme-color);
  display: block;
  font-size: 1em;
  text-decoration: none;
}
.publish-guide-popover__item .components-base-control__label {
  margin-bottom: 5px;
}
.publish-guide-popover__item .components-base-control {
  margin-bottom: 15px;
}
.publish-guide-popover__item .components-button.is-link:not(:disabled):not([aria-disabled=true]):not(.is-default):hover {
  color: var(--wp-admin-theme-color-darker-20) !important;
}
.publish-guide-popover__upload {
  position: relative;
}
.publish-guide-popover__upload > .components-button {
  margin-bottom: 20px;
  margin-right: 12px;
}
.publish-guide-popover__upload-button.components-button {
  background: rgba(0, 0, 0, 0.025);
  border: 1px dashed #949494;
  border-radius: 0;
  box-sizing: border-box;
  cursor: default;
  display: block;
  height: auto;
  line-height: 1.6;
  margin-bottom: 20px;
  margin-top: 20px;
  padding: 18px 0;
  position: relative;
  text-align: center;
  width: 100%;
}
.publish-guide-popover__upload-button--preview.components-button {
  border: none;
  margin-bottom: 12px;
  padding: 20px;
}
.publish-guide-popover__upload-button--preview.components-button img {
  display: block;
  margin: 0 auto;
  max-height: 80px;
}
.publish-guide-popover__upload-button:hover {
  cursor: pointer;
}
.publish-guide-popover__link {
  margin-top: 0.65em !important;
  text-decoration: none !important;
}
.publish-guide-popover:hover {
  border-color: var(--wp-admin-theme-color);
}
.publish-guide-popover__item:first-child {
  padding-top: 22px;
}
.publish-guide-popover .icon--external {
  margin-left: 5px;
}
.publish-guide-popover .is-primary.components-button {
  background-color: #1e1e1e;
  border-color: #1e1e1e;
  border-radius: 0;
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
  font-size: 15px;
  -webkit-font-smoothing: antialiased;
  font-weight: 600;
  height: 42px;
  line-height: 1;
  padding-bottom: 0;
  padding-left: 22px;
  padding-right: 22px;
  padding-top: 1px;
}
.publish-guide-popover .is-primary.components-button.is-busy, .publish-guide-popover .is-primary.components-button.is-busy:disabled, .publish-guide-popover .is-primary.components-button.is-busy[aria-disabled=true] {
  background-size: 100px 100%;
  color: #fff;
  /* stylelint-disable */
  background-image: linear-gradient(-45deg, var(--wp-admin-theme-color) 33%, var(--wp-admin-theme-color-darker-20) 33%, var(--wp-admin-theme-color-darker-20) 70%, var(--wp-admin-theme-color) 70%);
  /* stylelint-enable */
  border-color: var(--wp-admin-theme-color);
}
.publish-guide-popover__success {
  background-color: #f1f9f9;
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}
.publish-guide-popover__more-dropdown .components-button:focus:not(:disabled) {
  box-shadow: none !important;
}
.publish-guide-popover.components-animate__appear {
  animation-duration: 250ms;
  animation-iteration-count: 1;
  animation-name: PublishGuidePopover;
}
.publish-guide-popover .components-popover__content {
  border-color: #1e1e1e;
  box-shadow: rgba(0, 0, 0, 0.025) 0 0 0 1px, rgba(0, 0, 0, 0.075) 0 5px 20px 0, rgba(0, 0, 0, 0.025) 0 3px 3px 0;
  max-height: 650px !important;
  min-width: 300px;
}
.publish-guide-popover .components-modal__header {
  border-color: #1e1e1e;
  height: 56px;
  margin: 0;
  padding: 0 12px 0 20px;
}
.publish-guide-popover .components-modal__header h1 {
  color: #1e1e1e;
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
  font-weight: 500;
}

.publish-guide-popover__do-this-later {
  box-shadow: none !important;
  color: var(--wp-admin-theme-color);
  font-weight: 600;
  margin-left: 5px;
  outline: none !important;
  text-decoration: none;
}
.publish-guide-popover__do-this-later:hover {
  color: var(--wp-admin-theme-color-darker-20) !important;
}

.publish-guide-success {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
  justify-content: center;
  overflow: hidden;
  padding-top: 56px;
}
.publish-guide-success__content {
  align-items: center;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
}
.publish-guide-success__content > p {
  font-size: 1.2em;
  margin: 0 0 1em;
}
.publish-guide-success__svg {
  height: 115px;
  margin-bottom: 2.5em;
  margin-left: 2em;
}
.publish-guide-success__website {
  color: #1e1e1e !important;
  font-size: 1.25em !important;
  -webkit-font-smoothing: antialiased;
  font-weight: 600;
  text-decoration: none !important;
  transition: color 0.2s;
}
.publish-guide-success__website:hover {
  color: var(--wp-admin-theme-color) !important;
}
.publish-guide-success__social {
  display: flex;
  margin: 1.25em 0 0;
}
.publish-guide-success__social__item {
  margin: 0 0.5em;
}
.publish-guide-success__social__button {
  color: #1e1e1e !important;
  display: inline-block;
  margin: 0 0.4em;
  transition: color 0.2s;
}
.publish-guide-success__social__button:hover {
  color: var(--wp-admin-theme-color) !important;
}
.publish-guide-success__social svg {
  transform: scale(1.6);
}
.publish-guide-success__footer {
  border-top: 1px solid #1e1e1e;
  display: flex;
  flex-direction: column;
  margin-top: auto;
  padding: 14px 0;
  width: 100%;
}
.publish-guide-success__footer__button {
  color: #1e1e1e !important;
  display: flex;
  font-size: 1.1em !important;
  justify-content: space-between;
  padding: 8px 20px !important;
  text-decoration: none !important;
  transform: all 0.2s;
  width: 100%;
}
.publish-guide-success__footer__button:hover, .publish-guide-success__footer__button:active {
  color: var(--wp-admin-theme-color) !important;
}

.publish-guide-trigger {
  bottom: 32px;
  position: absolute;
  right: 32px;
  z-index: 2;
}
.publish-guide-trigger.is-shifted {
  right: 312px;
}
.publish-guide-trigger__button {
  align-self: center;
  border-radius: 100% !important;
  display: flex;
  height: 44px !important;
  justify-content: center;
  line-height: 44px;
  padding: 0;
  transition: all 100ms linear 0s;
  width: 44px;
}
.publish-guide-trigger__button:active {
  transform: scale(0.96);
}
.publish-guide-trigger__button > span {
  align-items: center;
  bottom: 0;
  display: flex;
  height: 44px;
  justify-content: center;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: opacity 90ms linear 0s, transform 180ms linear 0s;
  width: 44px;
  will-change: opacity, transform;
}
.publish-guide-trigger__button__icon-check {
  opacity: 1;
  transform: rotate(0deg) scale(1);
}
.publish-guide-trigger__button__icon-close {
  opacity: 0;
  transform: rotate(25deg) scale(1);
}
.publish-guide-trigger__button__pulse {
  z-index: -1;
}
.publish-guide-trigger__button__pulse span {
  animation-direction: normal;
  animation-duration: 2.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  animation-name: PublishGuidePulse;
  background: var(--wp-admin-theme-color);
  border-radius: 50%;
  display: block;
  height: 0;
  left: 0;
  padding-top: 100%;
  position: absolute;
  top: 0;
  width: 100%;
}
.publish-guide-trigger__button__pulse span:first-child {
  animation-delay: 0.33s;
}
.publish-guide-trigger__button.is-opened .publish-guide-trigger__button__icon-close {
  opacity: 1;
  transform: rotate(0deg) scale(1);
}
.publish-guide-trigger__button.is-opened .publish-guide-trigger__button__icon-check {
  opacity: 0;
  transform: rotate(0deg) scale(0);
}

.publish-guide-success p,
.publish-guide-success__svg {
  animation-delay: 50ms;
  animation-direction: normal;
  animation-duration: 400ms;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  animation-name: PublishGuideFadeUp;
  opacity: 0;
}

.publish-guide-success p:nth-child(2) {
  animation-delay: 200ms;
}
.publish-guide-success p:nth-child(3) {
  animation-delay: 400ms;
}
.publish-guide-success p:nth-child(4) {
  animation-delay: 600ms;
}

.publish-guide-success__confetti {
  margin-top: -10px;
}

.publish-guide-feedback {
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
}

.publish-guide-success__social li {
  animation-delay: 700ms;
  animation-direction: normal;
  animation-duration: 400ms;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
  animation-name: PublishGuideScaleFade;
  opacity: 0;
  transform: scale(0.5);
}
.publish-guide-success__social li:nth-child(2) {
  animation-delay: 900ms;
}
.publish-guide-success__social li:nth-child(3) {
  animation-delay: 1100ms;
}

.publish-guide-tooltip {
  transition: opacity 0.25s;
}
.publish-guide-tooltip--next {
  top: 100px;
  transition: top 1s;
}
.publish-guide-tooltip:not(.is-without-arrow)[data-y-axis=bottom]::before, .publish-guide-tooltip:not(.is-without-arrow)[data-y-axis=bottom]::after {
  border-bottom-color: #1e1e1e;
  top: -7px;
}
.publish-guide-tooltip:not(.is-without-arrow)[data-y-axis=top]::before, .publish-guide-tooltip:not(.is-without-arrow)[data-y-axis=top]::after {
  border-top-color: #1e1e1e;
  bottom: -7px;
}
.publish-guide-tooltip .components-popover__content {
  background-color: #1e1e1e;
  color: #fff;
  padding: 1em;
  position: relative;
  width: 250px;
}
.publish-guide-tooltip .components-popover__content .publish-guide-tooltip__title {
  color: #fff;
  font-size: 1.2em;
  margin: 0 0 0.5em;
}
.publish-guide-tooltip .components-popover__content .publish-guide-tooltip__close {
  background: none;
  border: 0;
  fill: #fff;
  position: absolute;
  right: 0;
  top: -0.15em;
  width: 1.25em;
}
.publish-guide-tooltip .components-popover__content .publish-guide-tooltip__close:hover {
  cursor: pointer;
}

.nextgen-button-pulsating {
  position: relative;
}
.nextgen-button-pulsating::before, .nextgen-button-pulsating::after {
  animation-direction: normal;
  animation-duration: 2.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: PublishGuidePulse;
  background: var(--wp-admin-theme-color);
  border-radius: 50%;
  content: "";
  display: block;
  height: 0;
  left: 0;
  opacity: 0;
  padding-top: 100%;
  position: absolute;
  top: 0;
  width: 100%;
}
.nextgen-button-pulsating::after {
  animation-delay: 0.33s;
}

body.rtl .publish-guide-trigger.is-shifted {
  left: 312px;
  right: unset;
}

.components-dropdown-menu__popover .components-popover__content {
  border-color: #1e1e1e;
  box-shadow: none;
}

@keyframes PublishGuidePulse {
  0% {
    opacity: 0.2;
    transform: scale(0);
  }
  50% {
    opacity: 0.2;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(2.25);
  }
}
@keyframes PublishGuidePopover {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes PublishGuideFadeUp {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  75% {
    transform: translateY(0);
  }
  100% {
    opacity: 1;
  }
}
@keyframes PublishGuideScaleFade {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}